      font-size: 28px;
      color: #F47920;
      padding-top: 16px;
    }
  }

  // People at moderate risk

  .plr__container {
    justify-content: space-between;
    .plr__title {
      font-size: 18px;
      color: #6b778c;
    }
    .plr__stat {
      font-size: 28px;
      color: #393938;
      padding-top: 16px;
    }
  }

}

