// USer Details 

.od__root {
  .od__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .od__title {
      font-size: 1.75rem;
      font-weight:700;
      letter-spacing: 0.05em;
    }
    .od__text {
      font-weight:400;
      font-size: 1.25rem;
      letter-spacing: 0.05em;
      line-Height: 28px;
      color:#393938;
    }
    .od__button {
      text-transform: capitalize;
    }
  }
}

// Stats Box 

.stat__box {
  cursor: pointer;
  transition: 0.3s ease-in-out;
  &:hover {
    transform: translateY(-8px);
  }
}


/* Tab Panel */
.tab__root {
  position: sticky;
  top: -1px;
  z-index: 99;
  background: #fcfcfc;
  .tabs {
    text-transform: capitalize;
    font-size: 14px;
    border-bottom: 1px solid rgba(171, 184, 202, 0.25);
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
    min-width: 16%;
  }
  .tab__selected {
    color: #0d809f !important;
    background: #e7f8fd;
  }

  .tab__extraBorder {
    min-width: 250px;
    border-bottom: 1px solid rgba(171, 184, 202, 0.25);

    @media (max-width: 900px) {
      min-width: 0px !important;
    }
  }
}

.od__root {
  .od__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .od__title {
      font-size: 1.7rem;
      letter-spacing: 0.05em;
    }
    .od__button {
      text-transform: capitalize;
    }
  }
}

