.rt__root {
  .rt__header {
    .rt__breadcrumbs {
      font-size: 14px;
      font-weight: normal;
      color: lightslategray;
    }
    .rt__title {
      font-size: 1.7rem;
      font-weight: 600;
      letter-spacing: 0.05em;
    }
    .rt__inner__box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 20px 0px;
      .rt__info__box {
        padding: 16px 24px;
        box-shadow: 0px 30px 40px rgba(212, 217, 232, 0.25);
        border-radius: 22px;
        min-width: 350px;
        .rt__inner__box__title {
          font-size: 1.3rem;
          font-weight: normal;
          padding: 10px 0px;
        }
        .list__item {
          display: flex;
          text-transform: capitalize;
          padding: 5px 0px;
          b {
            padding-right: 5px;
          }
        }
      }

    }
  }
}


.main_box_pd{
  padding: 16px;
}

  
.tab__root {
  position: sticky;
  top: -1px;
  z-index: 99;
  background: #fcfcfc;
  .tabs {
    text-transform: capitalize;
    font-size: 16px;
    border-bottom: 1px solid rgba(171, 184, 202, 0.25);
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
    min-width: 25%;
  }
  .tab__selected {
    color: #0d809f !important;
    background: #e7f8fd;
  }

  .tab__extraBorder {
    min-width: 250px;
    border-bottom: 1px solid rgba(171, 184, 202, 0.25);

    @media (max-width: 900px) {
      min-width: 0px !important;
    }
  }
}


/* ========== Download Report Box => Employee Report View ========== */

.report__files__box {
  box-shadow: 0px 1px 8px rgba(0,0,0,0.15);
  border-radius: 13px;
  padding: 10px 16px;
  .file__title {
    &::first-letter {
      text-transform: capitalize;
    }
  }
}