.tr-floating-card {
  z-index: 999999;
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  align-items: center;
  flex-direction: row;
  background: #FFFFFF;
  max-width: 440px;
  border: 1px solid #EEEEEE;
  border-radius: 20px;
  box-shadow: 0 4px 12px rgba(#000, 0.12);
  margin: 0 0 0 20px;
  // padding: 30px 20px;
  padding: 20px;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: 3s all cubic-bezier(0.445, 0.05, 0.55, 0.95);
  animation: float 3s cubic-bezier(0.445, 0.05, 0.55, 0.95) infinite;

  &:hover {
    animation: none;
    // transition: 3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  }
  
  &__thumbnail {
    width: 80px;
    max-width: 80px;
    min-width: 80px;
    // border: 1px solid #EEEEEE;
    border-radius: 100%;
    margin: 0 20px 0 0;
    padding: 4px;
    box-sizing: border-box;
    overflow: hidden;
    
    // img {
    //   display: block;
    //   width: 100%;
    //   max-width: 100%;
    //   border-radius: 100%;
    // }
  }

  &__content {
    min-width: 200px;
  }
  
  &__title {
    margin: 0 0 6px;
    padding: 0;
    color: #444444;
    font-size: 18px !important;
    font-weight: 700;
    line-height: 1;
    text-transform: capitalize;
    &:hover {
      cursor: pointer;
      text-decoration: underline;
    }
  }
  
  &__sub-title {
    margin: 0 0 6px;
    padding: 0;
    color: #999999;
    font-size: 12px !important;
    font-weight: 600;
    text-transform: capitalize;
    
    a {
      color: #CCCCCC;
      font-size: 12px;
      font-weight: 600;
      text-decoration: none;
    }
  }
  
  &__description {
    margin: 0 0 6px;
    padding: 0;
    color: #555;
    font-size: 14px;
    line-height: 20px;
  }
  
  &__footer {
    color: #666666;
    font-size: 12px;
    font-weight: 600;
    span {
      margin: 0 4px;
    }
    
    a {
      color: inherit;
      font-size: 10px;
      text-decoration: none;
      
      &:hover {
        text-decoration: underline;
      }
    }
  }
  
  @media (max-width: 480px) {
    flex-direction: column;
    text-align: center;
    
    &__thumbnail {
      margin: 0 0 20px;
    }
  }
}

