//TabPanel 

#report-tabpanel-0 {
  margin-top: 2%;
}


.fm__root {
  .fm__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .fm__title {
      font-size: 1.7rem;
      font-weight: 600;
      letter-spacing: 0.05em;
    }
    .fm__button {
      text-transform: capitalize;
    }
  }
}

// Form

.fm__textField {
  margin-top: 8px !important;

  div {
    border-radius: 13px !important;
  }
}

.fm__form__btn {
  text-transform: capitalize;
  font-size: 16px;
}

.fm__uploadFile_border {
  border: 1px dashed #0d809f;
  padding: 12px;
  border-radius: 13px;
  margin-top: 8px;
  position: relative;
  .fm__uploadFile {
    min-height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #f6fcfe;
    border-radius: 13px;
    cursor: pointer;
    svg {
      color: #0d809f;
    }
    p {
      color: #0d809f;
      /* padding: 20px 0px; */
      line-height: 60px;
      font-size: 18px;
    }
  }
  .fm__uploadFile__remove {
    position: absolute;
    top: 15px;
    right: 15px;
    svg {
      color: #0d809f;
      width: 20px;
      height: 20px;
    }
  }
}

/*  Family Members Lists */
.fm__fam__pad{
  padding: 16px;
}

.fm__fam__box {
  border: 1px solid #e6eaee;
  box-shadow: 0px 2px 12px rgba(21, 21, 22, 0.04);
  // min-height: 276px;
  border-radius: 13px;
  padding: 16px;
  padding-bottom: 8px;
  .fm_fam_avatar {
    font-size: 20px;
    background: #2d809f;
    margin-right: 16px;
    width: 72px;
    height: 72px;
  }
  .title {
    font-size: 20px;
    font-weight: 600;
    line-height: 20px;
    text-transform: capitalize;
    display: flex;
    flex-direction: column;
    span {
      font-size: 14px;
      text-transform: uppercase;
      color: gray;
      padding-top: 8px;
      font-weight: 500;
    }
  }
  .fm_fam_details {
    .chip {
      background: #fafeff;
      color: #0d809f;
      border: 1px solid #0d809f;
      margin-right: 10px;
      font-size: 12px;
    }
    .have__nothing {
      font-size: 14px;
      color: gray;
      font-weight: 500;
      letter-spacing: 0.8px;
    }
    .list__primary {
      font-size: 16px;
      font-weight: bold;
      color: #000;
      padding-right: 8px;
    }
    .list__secondary {
      font-size: 16px;
      color: gray;
    }
  }
  .fm__btn__box {
    display: flex;
    justify-content: flex-end;
    padding-top: 12px;
    padding-bottom: 8px;
    button {
      text-transform: capitalize;
      border: 1px solid;
      border-radius: 13px;
      padding: 6px 16px;
      font-size: 14px;
    }

    @media(max-width: 600px){
      flex-direction: column;
      // min-height: 110px;
      justify-content: space-between;
      button {
        margin: 0;
      }
      button:last-child {
        margin-top: 10px !important;
      }
    }
  }
}

// Family Members Profile View

.fm_prof__root {
  .fm_prof__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .fm_prof__breadcrumbs {
      display: flex;
      align-items: center;
      cursor: pointer;
      text-decoration: underline;
      svg {
        margin-right: 10px;
        color: #393938;
      }
      p {
        font-size: 18px;
        color: #393938;
      }
    }
  }

  /* Left View  */

  .fm_prof_left__root {
    .fm_prof_left__box {
      // min-height: 328px;
      padding: 16px;
      border: 1px solid #e6eaee;
      box-shadow: 0px 2px 12px rgba(21, 21, 22, 0.04);
      border-radius: 13px;

      .fm_prof_left__imgBox {
        img {
          display: block;
          margin: 0 auto;
          width: 180px;
          height: 180px;
          border-radius: 50%;
          border: 10px solid rgba(13, 128, 159, 0.12);
        }
      }

      .fm_prof_left__detailBox {
        .title {
          font-size: 28px;
          font-weight: 500;
          text-transform: capitalize;
        }
        .sub__title {
          font-size: 22px;
          font-weight: 500;
        }
        .desc__box {
          display: flex;
          align-items: center;
          svg {
            color: #1f8538;
            margin-right: 10px;
          }
          p {
            font-size: 16px;
            font-weight: normal;
          }
        }
      }
    }
  }

  /* Right View */

  .fm_prof_right__root {
    // padding: 16px;
    border: 1px solid #e6eaee;
    box-shadow: 0px 2px 12px rgba(21, 21, 22, 0.04);
    border-radius: 13px;
    .inner__box {
      .title {
        font-size: 22px;
        font-weight: 500;
      }
      .description {
        margin-top: 12px;
        font-size: 16px;
        font-weight: normal;
        line-height: 1.7;
        color: #393938;
        &::first-letter {
          text-transform: uppercase;
        }
      }
      .chip {
        background: #fafeff;
        color: #0d809f;
        border: 1px solid #0d809f;
        margin-right: 10px;
        font-size: 15px;
      }
      .timeAgo {
        font-size: 14px;
        color: gray;
        font-style: italic;
        position: relative;
        &:before {
          content: '';
          display: block;
          width: 75px;
          height: 1px;
          background: gray;
          left: -110%;
          top: 50%;
          position: absolute;
        }
      }
    }
    .footer__box {
      background: #f6fcfe;
      border: 1px solid #d0f2fb;
      box-shadow: 0px 2px 12px rgba(21, 21, 22, 0.04);
      border-radius: 13px;
      padding: 12px;
      display: flex;
      justify-content: flex-end;
      button {
        text-transform: capitalize;
        font-size: 16px;
        font-weight: normal;
        svg {
          font-size: 28px !important;
        }
      }
    }
    /* ORG Side footer */

    .org__footer__box {
      background: #f6fcfe;
      border: 1px solid #d0f2fb;
      box-shadow: 0px 2px 12px rgba(21, 21, 22, 0.04);
      border-radius: 13px;
      padding: 12px;
      display: flex;
      justify-content: flex-end;
      button {
        text-transform: capitalize;
        font-size: 16px;
        font-weight: normal;
        svg {
          font-size: 20px !important;
        }
      }
    }

    // TabPanel

    .tab__root {
      .tabs {
        text-transform: capitalize;
        font-size: 14px;
        border-bottom: 1px solid rgba(171, 184, 202, 0.25);
        border-top-right-radius: 8px;
        border-top-left-radius: 8px;
      }
      .tab__selected {
        color: #0d809f !important;
        background: #e7f8fd;
      }

      .tab__extraBorder {
        min-width: 250px;
        border-bottom: 1px solid rgba(171, 184, 202, 0.25);

        @media (max-width: 900px) {
          min-width: 0px !important;
        }
      }
    }

    // Employee Assign Screen

    .fm__prof__emp {
      min-height: 75vh;
      &__grid {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: 600px) {
          flex-direction: column;
          align-items: flex-start;
          justify-content: flex-start;
        }
      }
      &__box {
        border: 1px solid #e6eaee;
        box-shadow: 0px 2px 12px rgba(21, 21, 22, 0.04);
        border-radius: 13px;
        padding: 16px;
        width: 48.7%;
        @media (max-width: 600px) {
          width: 100%;
          margin-top: 16px;
        }
        &__title {
          font-size: 18px;
          font-weight: 500;
        }
        &__num {
          font-size: 28px;
          padding-top: 8px;
        }
      }

      &__table {
        border: 1px solid #e6eaee;
        box-shadow: 0px 2px 12px rgba(21, 21, 22, 0.04);
        border-radius: 13px;
        // padding: 16px;
        &__title {
          font-size: 18px;
          font-weight: 500;
        }
        &__root {
          max-height: 450px;
          border-radius: 8px;
          &::-webkit-scrollbar {
            width: 3px;
            height: 3px;
          }
          th {
            background: #2D809F;
            color: #fff;
          }
          td {
            text-transform: capitalize;
          }
        }
      }
    }
  }
}

/* Tab Panel */

.tab__root {
  position: sticky;
  top: -1px;
  z-index: 99;
  background: #fcfcfc;
  .tabs {
    text-transform: capitalize;
    font-size: 16px;
    border-bottom: 1px solid rgba(171, 184, 202, 0.25);
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
    min-width: 25%;
  }
  .tab__selected {
    color: #0d809f !important;
    background: #e7f8fd;
  }

  .tab__extraBorder {
    min-width: 250px;
    border-bottom: 1px solid rgba(171, 184, 202, 0.25);

    @media (max-width: 900px) {
      min-width: 0px !important;
    }
  }
}

/* ======== Style Dialog ======== */

.dia__left {
  background: #fcfcfc;
  .dia__left__inner__box {
    padding: 24px;
    .title {
      font-size: 18px;
      font-weight: 500;
    }
  }
  .dia_doc_avatar {
    background: #2d809f;
    font-size: 16px;
  }
  .chip {
    background: #fafeff;
    color: #0d809f;
    border: 1px solid #0d809f;
    margin-right: 10px;
    font-size: 12px;
    height: 24px;
  }
}

.dia__right {
  background: #fff;
  .dia__right__inner__box {
    padding: 18px 24px;
    .title {
      font-size: 18px;
      font-weight: 500;
    }
  }
}
